import React from 'react';
import styles from './BookingForm.module.css'; // Tạo một file CSS riêng cho BookingForm nếu cần

const BookingForm = () => {
  return (
    <div className={styles.bookingForm}>
      <div className={styles.tripType}>
        <label>
          <input type="radio" name="trip" value="one-way" defaultChecked /> Một chiều
        </label>
        <label>
          <input type="radio" name="trip" value="round-trip" /> Khứ hồi
        </label>
      </div>

      <div className={styles.formGroup}>
        <label>Điểm đi</label>
        <input type="text" placeholder="An Giang" />
      </div>

      <div className={styles.formGroup}>
        <label>Điểm đến</label>
        <input type="text" placeholder="Bình Phước" />
      </div>

      <div className={styles.formGroup}>
        <label>Ngày đi</label>
        <input type="date" />
      </div>

      <div className={styles.formGroup}>
        <label>Số vé</label>
        <select>
          <option value="1">1</option>
          <option value="2">2</option>
          <option value="3">3</option>
          <option value="4">4</option>
          <option value="5">5</option>
        </select>
      </div>

      <button className={styles.btnSearch}>Tìm chuyến xe</button>
    </div>
  );
};  
export default React.memo(BookingForm);
